@import '../base';

.c-article-diaries {
  position: relative;
  padding-top: 8.125rem;
  padding-bottom: 3.8125rem;
  z-index: 0;

  &__header-background {
    position: absolute;
    left: 0;
    width: 100%;
    top: 0;
    padding-bottom: 32.75rem;
    background-color: $s-color--background-tertiary;
    z-index: -1;
  }

  &__inner {
    @include t-gutters;
  }

  &__heading {
    color: $s-color--font-6;
    text-align: center;
    max-width: 74.375rem;
    width: 95%;
    margin: 0 auto 3.0625rem;
  }

  &__banner {
    position: relative;
    overflow: hidden;
    display: block;
    padding-bottom: 148.47%;
    cursor: pointer;

    &>* {
      pointer-events: none;
    }

    &:hover {

      .article-diaries__banner-play-button {
        transform: scale(1);
      }
    }

    &-image {
      @include t-absolute-center-x-y;

      &.is-desktop {
        display: none;
      }
    }

    &-inner {
      @include t-absolute-center-x();
      width: 100%;
      top: 32%;
    }

    &-excerpt {
      color: $s-color--font-6;
      text-transform: capitalize;
      text-align: center;
    }

    &-play-button {
      width: 4.5625rem;
      height: 4.5625rem;
      display: block;
      margin: 0 auto;
      transform: scale(.9);
      transition: transform $s-anim--time-short $s-anim--ease-in-out;
      margin-top: 0.8125rem;

      .icon--play {
        &-inner {
          fill: $s-color--background-tertiary;
        }
      }
    }
  }

  &__content {

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      @include t-font--heading-4;
      margin-top: 3.5625rem;
      margin-bottom: 2rem;
    }

    p,
    div {
      @include t-font--type-1;
      color: $s-color--font-8;
      margin-bottom: 1.25rem;
      line-height: 1.125rem;
    }

    >p,
    >div,
    >h1,
    >h2,
    >h3,
    >h4,
    >h5,
    >h6 {
      max-width: 58.9375rem;
      width: 100%;
      margin-left: auto;
      margin-right: auto;
    }
  }

  @include t-media-query($s-small-up) {
    &__banner {
      padding-bottom: 60%;
      max-width: 107.5rem;
      width: 90%;
      margin: 0 auto;
    }
  }


  @include t-media-query($s-medium-up) {
    padding-top: 15.875rem;

    &__header-background {
      padding-bottom: 52.75rem;
    }

    &__heading {
      margin: 0 auto 5.4375rem;
    }

    &__banner {
      padding-bottom: 45.47%;
      width: 100%;

      &-image {
        &.is-mobile {
          display: none
        }

        &.is-desktop {
          display: block
        }
      }

      &-inner {
        top: 35%;
      }

      &-play-button {
        width: 6.25rem;
        height: 6.25rem;
        margin-top: 3.375rem;
      }
    }

    &__content {
      padding-top: 3.75rem;

      h1,
      h2,
      h3,
      h4,
      h5,
      h6 {
        @include t-font--heading-5;
        margin-top: 3.6875rem;
        margin-bottom: 1.8125rem;
        margin-right: auto;
        margin-left: auto;
        text-align: left;
      }

      p,
      div {
        margin-bottom: 2.5rem;
        line-height: 1.625rem;
      }
    }
  }
}